*{
  box-sizing: border-box;
}

button{
  outline:none;
  border:0;
  cursor:pointer;
}

.navbar{
  font-size: 2rem;
  padding:1rem;
  background-color:blanchedalmond;
}

.navbar-logo{
  color:green;
  margin: right 0.5rem;
}

.navbar-count{
  display:inline-block;
  text-align:center;
  font-size: 2rem;
  width:2.5rem;
  height:2.5rem;
  line-height:2.5rem;
  margin-left:0.2rem;
  background: green;
  color:white;
  border-radius:50%;
}

.add-input{
  font-size:2rem;
  margin-right:0.2rem;
}

.add-button{
  height:100%;
  font-size:1.5rem;
  padding:0.3em 0.5em;
  background-color:green;
  color:white;
}



.habit{
  font-size:2.5rem;
  padding:0.5rem;
  list-style: none;
}

.habit-count{
  display:inline-block;
  text-align: center;
  font-size:2rem;
  width:2.5rem;
  height:2.5rem;
  line-height:2.5rem;
  margin-left:0.2rem;
  margin-right:0.5rem;
  background-color: darkseagreen;
  color:white;
  border-radius: 50%;
}
.habit-button{
  font-size:2.5rem;
  margin:0 0.1rem;
  background-color: transparent;
}

.habit-button:hover{
  opacity:0.8
}

.habit-increase,.habit-decrease{
  color:dodgerblue;
}

.habit-delete{
  color: tomato;
}

.habits-reset{
  font-size:1.5rem;
  padding:0.2em 1em;
  background-color: green;
  color:white;
}